#table{
    border: 2px solid black;
    width:500px;
    height:500px;
    background-color: yellow;
    margin-left:35%;
    border-collapse: collapse;
}

td
{
    border:5px solid blue;
    text-align: center;
    font-size: 75px;
    width:100px;
    height:100px;
    
}
td:hover
{
    cursor: pointer;
}
#information
{
    background-color: beige;
    height:auto;
    padding:30px;
    border-radius: 10px;
    position: fixed;
}
#resetbuttonz{
    background-color: green;
    border-radius: 10px;
    font-style: bold;

}
#resetbuttonz:hover{
    cursor: pointer;
    background-color: red;
}
#resetbuttonz:active{
    cursor: pointer;
    background-color: blue
}